import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";

import TechnicalOpinion               from "./TechnicalOpinion";
import { DEFAULT_FETCH_DATA, PARAMS } from "./TechnicalOpinion.constants";

const TechnicalOpinionContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {
	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { technicalOpinion }  = appraise;

	// ------------> hookForm <---------------
	const defaultValues = {	...technicalOpinion };

	const hookForm = useForm({
		defaultValues,
		shouldUnregister : true,
	});

	const { handleSubmit } = hookForm;

	const onSubmit =  handleSubmit(async (formData) => {
		await editApprasial({
			module : `/appraisals/technical-opinion/${appraiseId}`,
			method : "PUT",
			data   : formData,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<TechnicalOpinion
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(TechnicalOpinionContainer, PARAMS);
