import {
	Tabs,
	Quality,
	RoadWays,
	Equipment,
	RiskZones,
	HeaderPage,
	Instalations,
	Infrastructure,
	PlaceUnderConstruction,
	LivingTypeResults,
} from "Components/Appraisal";

const tabs = [
	{
		title : "Zonas de Riesgo",
		Tab   : RiskZones,
	},
	{
		title : "Vialidades",
		Tab   : RoadWays,
	},
	{
		title : "Infrestructura",
		Tab   : Infrastructure,
	},
	{
		title : "Obra Negra",
		Tab   : PlaceUnderConstruction,
	},
	{
		title : "Instalations",
		Tab   : Instalations,
	},
	{
		title : "Calidad",
		Tab   : Quality,
	},
	{
		title : "Equipamiento",
		Tab   : Equipment,
	},
	{
		title : "Tipo de Vivienda y Resultado  del Dictamen",
		Tab   : LivingTypeResults,
	},
];

const TechnicalOpinion = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[2, 2]}
				section="footer"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
				section="DICTAMEN TECNICO DE CALIDAD (MAI)"
			/>
		</>
	);
};

export default TechnicalOpinion;
